
/* Reset default styles */
body, h1, h2, h3, p, ul, li {
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f0f0f0;
  color: #333;
}

header {
  background-color: #333;
  color: #fff;
  padding: 10px 0;
}

nav ul {
  list-style-type: none;
  text-align: center;
}

nav ul li {
  display: inline-block;
  margin-right: 20px;
}

nav ul li a {
  text-decoration: none;
  color: #fff;
  font-weight: bold;
}

main {
  padding: 20px;
}

section {
  margin-bottom: 30px;
}

footer {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 10px 0;
}

/* Styles for specific components */
.project {
  background-color: #fff;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.project h2 {
  color: #333;
}

.project p {
  color: #666;
}

.project a {
  display: inline-block;
  margin-top: 10px;
  background-color: #333;
  color: #fff;
  padding: 5px 10px;
  border-radius: 3px;
  text-decoration: none;
}

.project a:hover {
  background-color: #555;
}

form {
  max-width: 400px;
  margin: 0 auto;
}

label {
  display: block;
  margin-bottom: 5px;
}

input,
textarea {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

button {
  background-color: #333;
  color: #fff;
  padding: 8px 15px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

button:hover {
  background-color: #555;
}
.home-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.intro-section {
  text-align: center;
  margin-bottom: 40px;
}

.projects-section,
.about-section,
.contact-section {
  margin-bottom: 40px;
}
.profile-image-container {
  width: 150px; /* Adjust the size as needed */
  height: 150px;
  border-radius: 50%; /* Creates a circular shape */
  overflow: hidden; /* Hides overflow content (e.g., if the image is larger than the container) */
  margin: 0 auto; /* Centers the image */
}

.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensures the image covers the entire container */
}
