/* App.css */

.App {
  position: relative;
  overflow: auto;
  height: 100vh;
}

/* Style for the AboutMe section */
.main-container {
  position: absolute;
  top: 100vh;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #f0f0f0; /* Change the background color as needed */
  padding: 20px;
  transition: top 0.3s ease; /* Add smooth transition effect */
}

/* Style for the Contact section */
.contact-container {
  position: absolute;
  top: 200vh; /* Adjust the top position based on your content height */
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #e0e0e0; /* Change the background color as needed */
  padding: 20px;
  transition: top 0.3s ease; /* Add smooth transition effect */
}

