/* Hide the scrollbar */
body {
  overflow: hidden;
}

/* Show scrollbar only when scrolling */
body::-webkit-scrollbar {
  width: 8px;
  background-color: transparent;
}

body::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 4px;
}

/* Show AboutMe section on scrolling up */
.App.scrolled-up .main-container {
  top: 0;
}

/* Show Contact section on scrolling further up */
.App.scrolled-up.scrolled-more .contact-container {
  top: 100vh;
}
